import {
	uniIcons
} from '../../../components/uni-icons/uni-icons.vue'
import {
	JSAPI,
	VOICEPROLOGUE,
	TEXTPROLOGUE,
	SETTEXTPROLOGUE,
	PROLOGUEGET,
	SETVOICEPROLOGUE,
	TTSSCENE
} from "../../../utils/api.js"
var jweixin = require('jweixin-module')
var jishiInterval;
import mpopup from "../../../components/xuan-popup/xuan-popup.vue"
var RECTIME
export default {
	components: {
		uniIcons,
		mpopup
	},
	data() {
		return {
			openInput: "",
			recStatus: "按住我开始录音",
			recComplete: false,
			isleft: true,
			isRight: false,
			progerssShow: false,
			recordTipShow: {
				opacity: "1"
			},
			isRec: false,
			ttsScene: '',
			isSave: false,
			isPlay: true,
			confirmShow: false,
			failShow: false,
			jsData: "",
			isDisable: true,
			localId: "",
			sceneName: '', //场景名称
			serverId: '',
			jishi: 0,
			xiaomiSucc: false,
			mobile: '',
			isJishi: false,
			startTime: '', //录音开始时间
			endTime: '', // 录音结束时间
			timeConsum: 0, //总耗时
			sceneId: '', //场景id
			PrologueGet: '', //场景详情
			ttsKey: '',
			sceneType: '',
			sceneDefId: '',

		}
	},
	onLoad(options) {
		uni.showToast({
			title: '加载中...',
			icon: 'none'
		})
		if (options.id) {
			this.sceneId = options.id
			// 编辑场景
			this.prologueGet()
		}
		// 得到JSSDK
		this.getJsAPI()
		// 查询当前音色
		this.checkTtsScene()
	},
	methods: {
		// 查询当前音色
		checkTtsScene() {
			const params = {}
			this.$request.url_request(TTSSCENE, params, 'GET', res => {
				uni.hideToast()
				this.ttsScene = JSON.parse(res.data).data.data
				console.log('sdadada', this.ttsScene)
			}, err => {})
		},
		// 编辑场景
		prologueGet() {
			const params = {
				id: this.sceneId
			}
			this.$request.url_request(PROLOGUEGET, params, 'GET', res => {
				this.PrologueGet = JSON.parse(res.data).data.data
				this.sceneName = this.PrologueGet.name
				this.openInput = this.PrologueGet.customText
				this.ttsKey = this.PrologueGet.ttsKey,
					this.sceneType = this.PrologueGet.sceneType
				this.sceneDefId = this.PrologueGet.sceneDefId
			}, err => {})
		},
		// 获得jssdk
		getJsAPI() {
			var that = this
			const params = {}
			this.$request.url_request(JSAPI, params, "GET", res => {
				this.jsData = JSON.parse(res.data).data
				console.log("开场白得到的签名", this.jsData.signature)
				jweixin.config({
						appId: this.jsData.appId,
						timestamp: this.jsData.timestamp,
						nonceStr: this.jsData.nonceStr,
						signature: this.jsData.signature,
						jsApiList: ['startRecord', 'stopRecord', 'playVoice', 'pauseVoice', 'stopVoice', 'uploadVoice',
							'onVoicePlayEnd'
						]
					}),
					jweixin.ready(function() {
						console.log("接口处理成功")
					}),
					jweixin.error(function() {
						console.log("接口处理失败")
					})
			}, err => {})
		},
		// 小秘说
		xiaomishuo() {
			this.isDisable = true
			console.log('自己录', this.isDisable)
			this.xiaomiSucc = false
			this.isleft = true,
				this.isRight = false
		},
		// 小秘取消
		cancelXiaomi() {
			this.xiaomiSucc = false
		},
		// 小米说保存
		saveXiaomi() {
			this.xiaomiSucc = true
		},
		// 小米说确认保存
		confirmXiaomi() {
			const params = {
				customText: this.openInput,
				name: this.sceneName,
				id: this.sceneId,
				ttsKey: this.ttsKey,
				sceneType: this.sceneType,
				sceneDefId: this.sceneDefId
			}
			this.$request.url_request(SETTEXTPROLOGUE, params, 'GET', res => {
				if (JSON.parse(res.data).code == 200) {
					uni.showToast({
						title: '保存成功！',
						icon: 'success',
						mask: true,
						duration: 1000
					})
					this.xiaomiSucc = false
					setTimeout(() => {
						uni.reLaunch({
							url: '../Setting/Setting'
						})
					}, 1000)
				} else {
					if (JSON.parse(res.data).resultMsg !== null || JSON.parse(res.data).resultMsg !== '') {
						this.xiaomiSucc = false
						uni.showToast({
							title: JSON.parse(res.data).resultMsg,
							icon: 'none',
							duration: 1200
						})
					}
				}
			}, err => {
				console.log("文本保存失败")
			})

		},
		// 小米说删除
		delXiaomi() {},
		// 自己录
		gotoSelfRecord() {
			console.log('自己录', this.isDisable)
			this.isDisable = false
			this.btnShow = false,
				this.isleft = false,
				this.isRight = true
		},
		// 开始录音
		startRec() {
			var that = this
			this.isJishi = true
			this.jishi = 0
			this.startTime = new Date().getTime()
			this.progerssShow = true,
				this.recordTipShow.opacity = "0",
				this.isRec = true,
				this.recStatus = "放开我停止录音",
				this.isPlay = true,
				console.log("开始录音")
			// 开始录音
			jweixin.startRecord()
			// 录音计时
			jishiInterval = setInterval(() => {
				that.jishi++
			}, 1000)

		},
		// 停止录音
		stopRec() {
			clearInterval(jishiInterval)
			this.isJishi = false
			this.endTime = new Date().getTime()
			this.timeConsum = this.endTime - this.startTime
			console.log(this.timeConsum)
			var that = this
			if (this.timeConsum / 1000 < 3) {
				uni.showModal({
					content: '录音不足3秒',
					showCancel: false
				});
			} else {
				var _this = this
				this.isSave = true,
					this.recComplete = true
				jweixin.stopRecord({
					success: function(res) {
						_this.localId = res.localId;
						uni.showToast({
							title: '录音完成',
							icon: 'none',
							mask: true,
							duration: 1000
						})
					}
				});
			}

			this.progerssShow = false,
				this.recordTipShow.opacity = "1",
				this.isRec = false,
				this.recStatus = "按住我开始录音",
				this.isPlay = false
		},
		// 录音试听
		playRecord() {
			var _this = this
			console.log("试听的localId", this.localId)
			if (this.localId == '') {
				uni.showModal({
					content: '请先录制一段声音！',
					showCancel: false
				});
			} else {
				jweixin.playVoice({
					localId: _this.localId,
				})
			}

		},

		// 删除录音
		delRec() {
			if (this.localId == '') {
				this.failShow = false
				uni.showModal({
					content: '请先录音！',
					showCancel: false
				});
			} else {
				this.isJishi = false
				this.confirmShow = true
			}
		},
		// 保存录音
		confirmSave() {
			if (this.localId == '') {
				this.failShow = false
				uni.showModal({
					content: '请先录音！',
					showCancel: false
				});
			} else {
				this.isJishi = false
				this.failShow = true
			}

		},
		// 上传语音 保存录音
		saveRecord() {
			var _this = this
			if (this.localId == '') {
				this.failShow = false
				uni.showModal({
					content: '请先录音！',
					showCancel: false
				});
			} else {
				console.log('得到的录音时间', this.timeConsum)
				jweixin.uploadVoice({
					localId: _this.localId, // 需要上传的音频的本地ID，由stopRecord接口获得
					isShowProgressTips: 1, // 默认为1，显示进度提示
					success: function(res) {
						_this.serverId = res.serverId; // 返回音频的服务器端ID
						const params = {
							serverId: _this.serverId,
							voiceLength: _this.timeConsum,
							name: _this.sceneName,
							id: _this.sceneId,
							ttsKey: _this.ttsScene.ttsKey,
							sceneType: _this.ttsScene.sceneType,
							sceneDefId: _this.ttsScene.sceneDefId,
							customText: "自定义语音开场白"
						}
						console.log('录音参数', params)
						_this.$request.url_request(SETVOICEPROLOGUE, params, 'GET', res => {
							if (JSON.parse(res.data).code == 200) {
								console.log("录音上传成功！")
								uni.showToast({
									title: '保存成功!',
									icon: 'success',
									duration: 1000
								})
								setTimeout(() => {
									uni.reLaunch({
										url: '../Setting/Setting'
									})
								}, 1000)
								_this.confirmShow = false,
									_this.failShow = false
								_this.isJishi = false
							} else {
								if (JSON.parse(res.data).resultMsg !== null || JSON.parse(res.data).resultMsg !== '') {
									this.xiaomiSucc = false
									uni.showToast({
										title: JSON.parse(res.data).resultMsg,
										icon: 'none',
										duration: 1200
									})
								}
							}

						}, err => {

							uni.showToast({
								title: '保存失败!',
								icon: 'none',
								duration: 1000
							})
							console.log("录音上传失败！")
						})
					}
				});
				this.confirmShow = false,
					this.failShow = false
			}
		},
		// 取消录音
		cancelSave() {
			this.isJishi = false
			this.confirmShow = false,
				this.failShow = false
		},
		confirmCancel() {
			this.isJishi = false
			this.confirmShow = false
		},
		// 返回上一页
		goback() {
			uni.navigateBack({
				delta: 1
			})
		}
	},
}
